<template>
    <div class="particulars">
        <van-nav-bar
            title="商品详情"
            :fixed="true"
            left-arrow
            @click-left="$router.go(-1)"
        />

        <div class="particulars-img">
            <img class="auto-img" :src="goodsData.large_img" alt="">
        </div>

        <div class="content">
            <div class="content-title">
                <h3 class="pro-text">{{ goodsData.name }}</h3>
                <span class="like" @click="likeGoods">
                    <img class="auto-img" :src="require('../assets/images/' + (isLike ? 'like_active.png' : 'like.png'))" alt="">
                </span>
            </div>

            <!-- 商品规格 -->
            <div class="rule-box" v-for="(item, index) in goodsData.rules" :key="index">
                <div class="rule-name">{{ item.title }}</div>
                <div class="temp">
                    <div class="temp-item" v-for="(val, i) in item.tag" :key="i" :class="{active: item.num == i}" @click="item.num = i">{{ val.name }}</div>
                </div>
            </div>

            <!-- 商品描述 -->
            <div class="goods-description">
                <div class="goods-text">商品描述</div>
                <p v-for="(item, index) in goodsData.desc" :key="index">{{ item }}</p>
            </div>

            <div class="pic">
                <div class="price">￥{{ goodsData.price }}</div>
                <van-stepper v-model="value" theme="round" button-size="20" disable-input />
            </div>
            
        </div>

        <!-- 商品购买导航栏 -->
        <van-goods-action>
            <van-goods-action-icon icon="cart-o" text="购物车" @click="$router.push({ name: 'Shopcart' })" :badge="shopcartNum == 0 ? null : shopcartNum" />
            <van-goods-action-button color="#439BF5" type="warning" text="加入购物车" @click="addShopcart" />
            <van-goods-action-button color="#1989FA" type="danger" text="立即购买" @click="addShopcart(true)" />
        </van-goods-action>
    </div>
</template>

<script>
    import { Toast } from 'vant';

    export default {
        data() {
            return {
                // 商品数量
                value: 1,
                // 商品id
                pid: null,
                // 收藏商品
                isLike: false,
                // 商品详情
                goodsData: {},
                // 购物车数量
                shopcartNum: null,
            };
        },

        methods: {

            // 根据商品id获取商品数据
            getProductDetails(pid) {

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/productDetail",
                    params: {
                        appkey: this.appkey,
                        pid,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 600) {
                        let data = res.data.result[0];
                        data.desc = data.desc.split(/\n/);

                        let goodsRule = ["tem", "milk", "sugar", "cream"];
                        let rules = [];

                        goodsRule.forEach((item) => {

                            if(data[item] != "") {
                                let o = {
                                    title: data[item + "_desc"],
                                    tag: [],
                                    num: 0,
                                }

                                let tag = data[item].split(/\//);
                                tag.forEach((val) => {
                                    let t = {
                                        name: val,
                                    };

                                    o.tag.push(t);

                                });

                                rules.push(o);
                            }

                        });

                        data.rules = rules;

                        this.goodsData = data;

                    }


                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();
                    

                });
            },

            // 收藏商品
            likeGoods() {

                // 获取token
                let tokenString = localStorage.getItem("Tk");
                let url = this.isLike ? "/notlike" : "/like";

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url,
                    data: {
                        appkey: this.appkey,
                        pid: this.pid,
                        tokenString,
                    }
                }).then(res => {
                    Toast.clear();

                    if(res.data.code == 800 || res.data.code == 900) {
                        this.isLike = res.data.code == 800;

                        this.isLike ? Toast.success(res.data.msg) : Toast.fail(res.data.msg);
                    } else{
                        Toast.fail("请先登录");
                        // token 无效,则跳转登录页面
                        this.$router.push({ name: "Login" });
                    }

                }).catch(err => {
                    Toast.clear();


                });

            },

            // 查询商品是否被收藏
            findLike(pid) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                this.axios({
                    method: "GET",
                    url: "/findlike",
                    params: {
                        appkey: this.appkey,
                        pid,
                        tokenString,
                    }
                }).then(res => {

                    if(res.data.code == 1000) {

                        this.isLike = res.data.result.length > 0;

                    }

                }).catch(err => {


                });
            },

            // 查询购物车数量
            findShopcart() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                this.axios({
                    method: "GET",
                    url: "/findAllShopcart",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {

                    if(res.data.code == 5000) {
                        this.shopcartNum = res.data.result.length;
                    }

                }).catch(err => {


                });
            },

            // 加入购物车
            addShopcart(buy) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                let rules = this.goodsData.rules;

                let rule = [];
                rules.forEach((item) => {
                    rule.push(item.tag[item.num].name);
                });

                rule = rule.join("/");

                this.axios({
                    method: "POST",
                    url: "/addShopcart",
                    data: {
                        // 商品id
                        pid: this.pid,
                        // 商品数量
                        count: this.value,
                        // 商品规格
                        rule,

                        appkey: this.appkey,
                        tokenString,
                    },
                }).then(res => {
                    Toast.clear();

                    if(res.data.code == 3000) {
                        if(res.data.status == 1) {
                            this.shopcartNum++;
                        }

                        Toast.success(res.data.msg);

                        // 如果是立即购买
                        if(buy === true) {

                            setTimeout(() => {
                                this.$router.push({ name: "Pay", query: {sids: res.data.sid} })
                            }, 500);
                            
                        }

                    }else{
                        
                        this.$router.push({ name: "Login" });

                        Toast.fail("请先登录");
                    }

                }).catch(err => {
                    Toast.clear();


                });

            }
        },

        created() {
            this.pid = this.$route.query.pid;
            // 商品详情数据
            this.getProductDetails(this.pid);
            // 查询商品是否被收藏
            this.findLike(this.pid);
            // 查询购物车数量
            this.findShopcart();
        }
    }
</script>

<style lang="less" scoped>
    .particulars{
        height: calc(100vh - 45px);
        overflow-y: auto;

        .van-nav-bar{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 45px;
        }
    }

    .particulars-img{
        margin-top: 45px;
    }

    .content{
        padding: 10px;
        color: #666;
        background-color: #fff;

        .content-title{
            display: flex;
            height: 35px;
            line-height: 35px;
            justify-content: space-between;

            .pro-text{
                font-size: 16px;
            }

            .like{
                width: 25px;
                height: 25px;
            }
        }

        .rule-box{
            display: flex;
            padding: 5px 0;
            font-size: 14px;
            line-height: 25px;

            .rule-name{
                width: 50px;
            }

            .temp{
                display: flex;
                text-align: center;
                font-size: 12px;
                flex-wrap: wrap;

                .temp-item{
                    width: 60px;
                    height: 25px;
                    margin: 0 0 5px 5px;
                    border-radius: 15px;
                    background-color: #eee;

                    &.active {
                        color: #f0f0f0;
                        background-color: #3D96F2;
                    }
                }
            }
        }

        .goods-description{
            margin-top: 10px;
            padding: 10px 0;
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;

            .goods-text{
                font-size: 16px;
                margin-bottom: 5px;
            }

            p{
                color: #7c7c7c;
                font-size: 14px;
                line-height: 25px;
            }
        }

        .pic{
            display: flex;
            padding: 15px 0 5px;
            justify-content: space-between;

            .price{
                color: #1989FA;
                font-size: 16px;
            }

            /deep/ .van-stepper__minus{
                color: #1989FA;
                border-color: #1989FA;
            }

            /deep/ .van-stepper__plus{
                background-color: #1989FA;
            }
        }
    }

    .van-goods-action{
        height: 45px;
        box-shadow: 0 1px 4px 0 #ccc;

        .van-goods-action-icon {
            margin: 0 10px;
        }

        .van-goods-action-button {
            height: 35px;
        }

        .van-goods-action-button--first{
            margin-left: 10px;
        }

        .van-goods-action-button--last{
            margin-right: 10px;
        }
    }
</style>